import { message } from 'antd'
import React, { useEffect, useState } from 'react'
import { Modal } from 'antd'
import { Link, useNavigate } from 'react-router-dom'
import Register from '../../pages/Register'
import Login from '../../pages/Login'
import Dropdown from 'react-bootstrap/Dropdown';
import logo from '../../img/ATG logo.png'
import clipart from '../../img/clipart.png'
import 'bootstrap/dist/css/bootstrap.min.css'; // Import Bootstrap CSS
import { FaSearch } from 'react-icons/fa'; // Import the search icon from react-icons library



const Header = () => {
  const [showModal, setShowModal] = useState(false)
  const [signupModel, setSignupModel] = useState(null)
  const navigate = useNavigate()

  const [loginUser, setLoginUser] = useState('')
  useEffect(() => {
    const user = JSON.parse(localStorage.getItem('user'))
    if (user) {
      setLoginUser(user)
    }
  }, [])

  const logoutHandler = () => {
    localStorage.removeItem("user")
    message.success("Logout Successfully")
    window.location.reload();

  }


  return (
    <>

      <nav className="navbar navbar-expand-lg  m-auto bg-white" style={{ width: '1440px', height: '72px' }}>
        <div className="container-fluid">
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarTogglerDemo01" aria-controls="navbarTogglerDemo01" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon" />
          </button>
          <div className="collapse navbar-collapse" id="navbarTogglerDemo01">
            <img src={logo} alt="Logo" style={{ width: '162.57px', height: '24px', top: '24px', left: '72px' }} />


            <div className='m-auto '>
              <div className="input-group flex-nowrap border-1" style={{ backgroundColor: "#F2F2F2", width: '360px', height: '42px', borderRadius: '21px' }}>

                <span className="input-group-text bg-transparent border-0 " id="addon-wrapping">
                  <FaSearch color='gray' />
                </span>
                <input
                  type="text"
                  className="form-control bg-transparent border-0"
                  placeholder="Search for your favorite groups in ATG"
                  aria-label="Search"
                  aria-describedby="addon-wrapping"
                />
              </div>
            </div>


            <ul className="navbar-nav  mb-2 mb-lg-0">
              {loginUser ? (
                <li className="nav-item">
                  <div className="d-flex">
                    <img src={clipart} alt="Logo" style={{ width: '36px', height: '36px' }} />
                    <h6 className="mt-2">{loginUser.name}</h6>
                    <Dropdown>
                      <Dropdown.Toggle variant="" id="dropdown-basic"> </Dropdown.Toggle>
                      <Dropdown.Menu>
                        <Dropdown.Item onClick={logoutHandler}>Logout</Dropdown.Item>
                      </Dropdown.Menu>
                    </Dropdown>
                  </div>

                </li>
              ) : (
                <li className="nav-item">
                  <div className='d-flex'>
                    <h6 className='mt-2'  >Create account. <span className='text-primary'>It's free!</span></h6>
                    <Dropdown>
                      <Dropdown.Toggle variant="" id="dropdown-basic">

                      </Dropdown.Toggle>

                      <Dropdown.Menu>
                        <Dropdown.Item onClick={() => { setShowModal(true); setSignupModel(true); }}>Sign Up</Dropdown.Item>
                        <Dropdown.Item onClick={() => { setShowModal(true); setSignupModel(false); }}>Sign In</Dropdown.Item>

                      </Dropdown.Menu>
                    </Dropdown></div>


                </li>
              )}
            </ul>
          </div>



          {/* Model */}
          <Modal title={"Let's learn, share & inspire each other with our passion for computer engineering. Sign up now "}
            open={showModal} onCancel={() => setShowModal(false)} footer={false} width={800} > 

            <div>
             
              {signupModel ? (<Register></Register> ) : (<Login ></Login>)}

            </div>

          </Modal>
        </div>

      </nav>
    </>
  )
}

export default Header;